<template>
	<div class="supplier-view">
		<div class="separator-box">
			<el-breadcrumb class="separator" separator-class="el-icon-arrow-right">
				<el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item>{{ $route.params.supplier }}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="supplier-box">
			<goods-item v-for="goods in supplierList" :key="goods.id" :goods="goods"></goods-item>
		</div>
	</div>
</template>

<script>
import { getSupplierListApi } from '@/api/goods';
import GoodsItem from '@/components/GoodsItem';

export default {
	name: 'supplier-view',
	components: {
		GoodsItem,
	},
	data() {
		return {
			supplierList: [],
		};
	},
	beforeRouteEnter(to, from, next) {
		next(vm => {
			vm.getSupplierList(to);
		});
	},
	methods: {
		async getSupplierList(to) {
			let res = await getSupplierListApi({ supplier: to.params.supplier });
			this.supplierList = res;
		},
	},
};
</script>

<style scoped>
.supplier-box {
	margin: 0 -10px;
	display: flex;
	flex-wrap: wrap;
}
</style>
